import React, { useState, useEffect, useRef } from 'react';
import io from 'socket.io-client';
import styled from 'styled-components';
import ChatContext from '../components/ChatContext';
import RefreshVerification from '../server/RefreshVerification';

//RefreshVerification.verification();

let data = localStorage.getItem("token");
let save_token = JSON.parse(data) && JSON.parse(data).access_token;
let save_refresh_token = JSON.parse(data) && JSON.parse(data).refresh_token;
let save_user_idx = JSON.parse(data) && JSON.parse(data).user_idx;
let save_user_name = JSON.parse(data) && JSON.parse(data).user_name;

const socket = io('http://3.17.55.178:3002/', {
    auth: {
        token: save_token,
    },
});

socket.on('connect', () => {
    console.log('chatting connection server');
});

const Chatting = (props) => { 
    console.log("room_idx : " + parseInt(props.room_idx));
    // console.log("save_token : " + save_token);
    // console.log("save_user_idx : " +save_user_idx);
    // console.log("save_user_name : " +save_user_name);

    const [recentColor, setRecentColor] = useState('white'); //   
    
    //     
    const [inputMessage, setInputMessage] = useState('');

    //        
    const [emitMessage, setEmitMessage] = useState(false);

    //                    

    //      UI   
    const [chatMonitor, setChatMonitor] = useState([]);

    //       
    const [onMessage, setOnMessage] = useState(false);

    //     ( )   
    const [recentChat, setRecentChat] = useState("");

    //   ( )    
    const [recentChatUserName, setRecentChatUserName] = useState('');

    //   ( )    
    const [recentChatUserIdx, setRecentChatUserIdx] = useState('');

    //   
    const handleInput = (e) => {
        setInputMessage(e.target.value);
    };

    //  ,    
    const handleEnter = (e) => {
        if (e.key === 'Enter') {
            if (inputMessage.length > 0) {
                let room_idx = parseInt(props.room_idx);
                let user_idx = parseInt(save_user_idx);
                let user_name = save_user_name;
                let msg = inputMessage;

                let getMsgInfo = {
                    room_idx: room_idx,
                    user_idx: user_idx,
                    user_name: user_name,
                    msg: msg
                }

                socket.emit("chat", getMsgInfo);

                setEmitMessage(!emitMessage);
                setInputMessage('');
            }
            
        }
    };

    //    ,    
    const handleSubmit = () => {
        if (inputMessage.length > 0) {
            let room_idx = parseInt(props.room_idx);
            let user_idx = parseInt(save_user_idx);
            let user_name = save_user_name;
            let msg = inputMessage;

            let sendMsgInfo = {
                room_idx: room_idx,
                user_idx: user_idx,
                user_name: user_name,
                msg: msg
            }

            socket.emit("chat", sendMsgInfo);

            setInputMessage('');
            setEmitMessage(!emitMessage);
        }
    };

    //       (   )

    useEffect(() => {
        setRecentColor(props.color);

        socket.on("chat", (data) => {

            setRecentChatUserIdx(data.user_idx);
            setRecentChatUserName(data.user_name);
            setRecentChat(data.msg);
        
            setOnMessage(true);

        });

    }, []);

    //    
    const scrollToBottom = () => {
        document.getElementById('chatMonitor').scrollBy({ top: 100 });
    };

    //  ,    , async, await         
    useEffect( () => {
        const scrollUpdate = async () => {
        //     
        (await recentChat.length) > 0 && setChatMonitor([...chatMonitor, {recentChat, recentChatUserName, recentColor}]); 
        
        // await    
        scrollToBottom();
        setRecentChat('');
        };

        scrollUpdate();
    }, [recentChat]);

  return (
    <React.Fragment>
            <Container style={{height : props.height}}>
                <ChatContainer id="chatMonitor">
                    {/* 18   */}
                    {chatMonitor.map((values, index) => {          
                        return (<ChatContext key={index} name={values.recentChatUserName} color={recentColor} text={values.recentChat}></ChatContext>);
                    })}
                </ChatContainer>
                <InputMsgContainer>
                {props.available?
                    <InputMsg
                        type="text"
                        placeholder="   "
                        value={inputMessage}
                        onChange={handleInput}
                        onKeyPress={handleEnter}
                    ></InputMsg> :
                    <InputMsg
                        type="text"
                        placeholder="   "
                        value={inputMessage}
                        onChange={handleInput}
                        onKeyPress={handleEnter}
                        disabled={true}
                    ></InputMsg>

                }
                    <InputMsgBtn onClick={handleSubmit}></InputMsgBtn>
                </InputMsgContainer>
            </Container>
    </React.Fragment>
);
};

const Container = styled.div`
    width: 220px;
    height: 620px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: white;
    opacity: 0.7;
`;

const ChatContainer = styled.div`
    width: 200px;
    height: 520px;
    padding: 10px;
    margin-bottom: 20px;
    background-color: rgba( 0, 0, 0, 0.5 );
    border-radius: 0.5rem;
    // overflow-y: scroll;
    // overflow-x:hidden;
    overflow: auto;
    &::-webkit-scrollbar {
        /*    */
        width: 10px;

        /*    */
        height: 10px;

        border-radius: 6px;
        background: rgba(255, 255, 255, 0.4);
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 6px;
    }
`;

const InputMsgContainer = styled.div`
    width: 200px;
    height: 80px;
    padding: 10px;
    background-color: rgba( 0, 0, 0, 0.5 );
    border-radius: 0.5rem;
    display: flex;
    justify-content: space-between;
`;

const InputMsg = styled.textarea`
    width: 160px;
    height: 60px;
    padding: 10px;
    background-color: #000000;
    border-radius: 0.5rem;
    margin-right: 7px;
    border: none;
    outline: none !important;
    resize: none;

    ::placeholder {
        color: white;
        font-weight: bold;
    }
    color: white;

    overflow-y: scroll;
    &::-webkit-scrollbar {
        /*    */
        width: 10px;

        /*    */
        height: 10px;

        border-radius: 6px;
        background: rgba(255, 255, 255, 0.4);
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 6px;
    }
`;

const InputMsgBtn = styled.div`
    width: 40px;
    height: 80px;
    background-color: #000000;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
`;

export default Chatting;